<template>
    <BaseAccount>
        <div v-if="!userProjects.length">
            Проекты не найлены
        </div>
        <div v-else v-for="(item, index) in userProjects">
            <div class="row">
                <div class="col-md-1">
                    {{ index + 1 }}
                </div>
                <div class="col-md-7">
                    <router-link :to="{ path: `/projects/${item.id}` }">{{ item.title }}</router-link>
                </div>
                <div class="col-md-2">
                    <a href="#" class="text-success">Редактировать</a>
                </div>
                <div class="col-md-2">
                    <a href="#" class="text-danger" @click.prevent="deleteHandler(item.id)">Удалить</a>
                </div>
            </div>
            <hr>
        </div>
    </BaseAccount>
</template>

<script>
    import BaseAccount from '@/components/BaseAccount.vue';
    import { mapActions, mapState } from 'vuex';

    export default {
        name: "AccountProjects",
        components: {
            BaseAccount
        },
        computed: {
            ...mapState({
                userProjects: state => state.userProjects
            })
        },
        methods: {
            ...mapActions([
                'getUserProjects',
                'toggleLoad',
                'deleteProject'
            ]),
            deleteHandler(id) {
                this.toggleLoad(true);

                this.deleteProject(id).then(() => {
                    this.getUserProjects().finally(() => {
                        this.toggleLoad(false);
                    });
                });
            }
        },
        created() {
            this.toggleLoad(true);
            this.getUserProjects().finally(() => {
                this.toggleLoad(false);
            });
        }
    }
</script>
